.app {
  /* border: 1px solid red; */
  width: 50rem;
  padding: 2rem;
  box-shadow: 0 1.2rem 3.2rem rgba(0, 0, 0, 0.1);
  border-radius: 9px;
  position: relative;
  background-color: #fff;
}
.app__heading {
  font-family: "Poppins", sans-serif;
}
.app__heading--primary {
  font-size: 3.2rem;
  text-align: center;
  margin-bottom: 1.6rem;
}
.app__heading--secondary {
  font-size: 1.8rem;
  margin-bottom: 1.6rem;
}

.app__btn {
  position: absolute;
  top: 0;
  right: 0;
  /* padding: 1.8rem; */
  height: 6rem;
  width: 6rem;
  border-radius: 50%;
  font-size: 2.8rem;
  background-color: rgb(27, 105, 27);
  border: none;
  color: white;
  transform: translate(40%, -40%);
  cursor: pointer;
}
.app__input {
  font-family: "Caveat", cursive;
  width: 100%;
  /* width: 95%; */
  padding: 1.2rem 1.8rem;
  font-size: 2rem;
  color: #555;
  border-radius: 6px;
  border: none;
  outline: none;
  background-color: #eee;
  margin-bottom: 3.2rem;
}
.app__tasks {
  list-style: none;
}
.app__task {
  font-size: 2.4rem;
  padding: 1.2rem 0;
  font-family: "Caveat", cursive;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.app__btn--delete {
  background-color: transparent;
  border: none;
  color: red;
  font-size: 1.8rem;
  cursor: pointer;
}
